<template>
	<v-container>
		<v-row>
			<v-col>
				<v-card outlined>
					<v-card-title class="mb-2">   </v-card-title>
					<v-card-subtitle class="pb-0">
						         .
					</v-card-subtitle>
					<v-card-subtitle class="pt-0">
						        .
					</v-card-subtitle>

					<v-card-text>
						<ValidationObserver ref="observer2">
							<ValidationProvider
								v-slot="{ errors }"
								name=""
								:rules="{ required: true }"
							>
								<v-text-field
									v-model="name"
									label=""
									outlined
									solo
									dense
									:error-messages="errors"
								/>
							</ValidationProvider>
							<ValidationProvider
								v-slot="{ errors }"
								name=""
								:rules="{ required: true, email: true }"
							>
								<v-text-field
									v-model="email"
									label=""
									solo
									outlined
									dense
									:error-messages="errors"
								/>
							</ValidationProvider>
							<v-row>
								<v-col cols="6">
									<ButtonCmp
										color="teal"
										width="100%"
										large
										btnTxtClass="white--text"
										@click="sendPhone"
									>
										 
									</ButtonCmp>
								</v-col>
								<v-col cols="6">
									<ButtonCmp
										color="teal"
										width="100%"
										large
										btnTxtClass="white--text"
										@click="sendEmail"
									>
										 
									</ButtonCmp>
								</v-col>
							</v-row>
						</ValidationObserver>
					</v-card-text>
				</v-card>
			</v-col>
		</v-row>
	</v-container>
</template>

<script>
	import { ValidationProvider, ValidationObserver, extend } from 'vee-validate';
	import { email, required, numeric } from 'vee-validate/dist/rules';
	import ButtonCmp from '@/components/ButtonCmp.vue';
	// extend('max', () => {
	//   ...max
	//   console.log('hi');
	// });
	extend('email', {
		...email,
		message: '{_field_}  .',
	});
	extend('required', {
		...required,
		message: '{_field_}()  .',
	});
	extend('numeric', {
		...numeric,
		message: '{_field_} ',
	});

	export default {
		components: {
			ValidationProvider,
			ValidationObserver,
			ButtonCmp,
		},
		created() {},
		data() {
			return {
				name: '',
				email: '',
			};
		},
		methods: {
			sendPhone() {
				this.$refs.observer2.validate();
			},
			sendEmail() {
				this.$refs.observer2.validate();
			},
		},
	};
</script>

<style></style>
